import React , { Component } from 'react';
import Grid from 'material-ui/Grid'

export default class Square extends Component {

	render() {
		return(
			<div>
				<Grid container>
					<Grid item>
						{this.props.children}
					</Grid>
				</Grid>
			</div>
		);
	}
}


